import {get, post} from '@/util/api';

const state = {};
const getters = {};
const actions = {
    async get() {
        return await get('subjects');
    },

    async create(context, subject) {
        return await post('subjects', subject);
    }
};
const mutations = {};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
}
