import {API_BASE} from '@/util/consts';

const state = {
    authenticated: false,
};
const getters = {
    isLoggedIn({authenticated}) {
        return authenticated;
    },
};
const actions = {
    async check({commit, state}) {
        const response = await fetch(`${API_BASE}/auth`, {credentials: 'include'})
            .then(response => response.json());

        commit(response === 'Ok' ? 'login' : 'logout');

        return state.authenticated;
    },

    async login({commit}, {email, password, remember}) {
        const response = await
            fetch(`${API_BASE}/auth`, {
                method: 'POST',
                credentials: 'include',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    Email: email,
                    Password: password,
                    RememberMe: remember ? '1' : '0',
                }),
            })
                .then(response => response.json());
        commit('login');

        return response;
    },

    async logout({commit}) {
        const response = await
            fetch(`${API_BASE}/auth`, {
                method: 'DELETE',
                credentials: 'include',
            })
                .then(response => response.json());
        commit('logout');

        return response;
    },
};
const mutations = {
    login(state) {
        state.authenticated = true;
    },
    logout(state) {
        state.authenticated = false;
    },
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
}
