import {get, post} from '@/util/api';

const state = {};
const getters = {};
const actions = {
    async get() {
        return await get('audiences');
    },

    async create(context, audience) {
        return await post('audiences', audience);
    }
};
const mutations = {};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
}
