import {API_BASE} from '@/util/consts';

export async function get(endpoint) {
    return await
        fetch(`${API_BASE}/${endpoint}`, {credentials: 'include'})
            .then(response => response.json());
}

export async function post(endpoint, content) {
    return await
        fetch(`${API_BASE}/${endpoint}`, {
            method: 'POST',
            credentials: 'include',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(content),
        })
            .then(response => response.json());
}

export async function put(endpoint, content) {
    return await
        fetch(`${API_BASE}/${endpoint}`, {
            method: 'PUT',
            credentials: 'include',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(content),
        })
            .then(response => response.json());
}

export async function del(endpoint) {
    return await
        fetch(`${API_BASE}/${endpoint}`, {
            method: 'DELETE',
            credentials: 'include',
        })
            .then(response => response.json());
}

export async function getScheduleTimes(housingId = null) {
    let endpoint = `${API_BASE}/scheduletimes`;
    if (housingId !== null) {
        endpoint = `${endpoint}?housingId=${housingId}`;
    }

    return await
        fetch(endpoint, {credentials: 'include'})
            .then(response => response.json())
}

export async function getHousings(housingId = null) {
    let endpoint = `${API_BASE}/housings`;
    if (housingId !== null) {
        endpoint = `${endpoint}/${housingId}`;
    }

    return await
        fetch(endpoint, {credentials: 'include'})
            .then(response => response.json())
}


export async function getGroups(groupId = null, housingId = null) {
    let endpoint = `${API_BASE}/groups`;
    if (groupId !== null) {
        endpoint = `${endpoint}/${groupId}`;
    }
    if (housingId !== null) {
        endpoint = `${endpoint}?housingId=${housingId}`;
    }

    return await
        fetch(endpoint, {credentials: 'include'})
            .then(response => response.json())
}

export async function getSchedules(scheduleId = null, groupId = null) {
    let endpoint = `${API_BASE}/schedules`;
    if (scheduleId !== null) {
        endpoint = `${endpoint}/${scheduleId}`;
    }
    if (groupId !== null) {
        endpoint = `${endpoint}?groupId=${groupId}`;
    }

    return await
        fetch(endpoint, {credentials: 'include'})
            .then(response => response.json())
}

export async function postSchedules(schedule) {
    let endpoint = `${API_BASE}/schedules`;
    if (schedule.Id !== null) {
        endpoint = `${endpoint}/${schedule.Id}`;
    }
    return await
        fetch(endpoint, {
            method: schedule.Id ? 'PUT' : 'POST',
            credentials: 'include',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(schedule),
        })
            .then(response => response.json());
}

export async function deleteSchedules(scheduleId) {
    return await
        fetch(`${API_BASE}/schedules/${scheduleId}`, {
            method: 'DELETE',
            credentials: 'include',
        })
            .then(response => response.json());
}
